import React from "react";
import Feature from "./Feature";
import taxIcon from "../../icons/tax.svg";

function Features() {
  const features = {
    heading: "Bookkeeping",
    content:
      "Nanotechnology immersion along the information highway will close the loop on focusing on the line.",
    icon: taxIcon,
  };

  return (
    <section className="px-4 pb-20 pt-28 bg-gray-50">
      <div className="pb-16 mx-4 border-b-2 border-gray-300">
        <div className="flex items-center mt-6">
          <div className="w-12 h-0.5 mr-3 bg-primary"></div>
          <span className="text-gray-800 ">Done fast, done right</span>
        </div>
        <h1 className="mt-1 text-4xl font-light font-montserrat">
          Making Taxes look easy
        </h1>
        <p className="w-full mt-5 font-medium md:w-3/5">
          Bring to the table win-win survival strategies to ensure proactive
          domination. At the end of the day, going forward, a new normal that
          has evolved from generation.
        </p>
      </div>
      <div className="flex flex-wrap w-full pt-16">
        <Feature width="w-1/3" {...features} />
        <Feature width="w-1/3" inverted {...features} />
        <Feature width="w-1/3" {...features} />
        <Feature width="w-1/3" inverted {...features} />
        <Feature width="w-1/3" {...features} />
        <Feature width="w-1/3" inverted {...features} />
      </div>
    </section>
  );
}

export default Features;
